	max-width: 1170px;
	margin:auto;
}
.footer-col{
    width: 25%;
    padding: 0 15px;
    
    
 }
 .row{
	display: flex;
	flex-wrap: wrap;
}
ul{
	list-style: none;
}
.footer-col h4{
	font-size: 18px;
	color: #ffffff;
	text-transform: capitalize;
	margin-bottom: 35px;
	font-weight: 500;
	position: relative;
}
.footer-col h4::before{
	content: '';
	position: absolute;
	left:0;
	bottom: -10px;
	background-color: rgb(0, 212, 212);
	height: 2px;
	box-sizing: border-box;
	width: 50px;
}
.footer{
    background-color:#24262b ;
    padding: 70px 0;
    width: 100%;
    height: 200px;
    display: table;
    
}
